    margin-top:500px
  }
  .section1{
    margin-top:2200px;
  }

  .six{
    margin-top:7100px;
  }
  .footer{
    top:7850px;
    background-color: $gradiant;
    background-size: 100%;
    background-position: center;
    height: 800px;
  }
}